  font-weight: 400;
  margin-bottom: 25px;
  line-height: 1.6em;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.6);
}

.ResetButton {
  border: 0;
  cursor: pointer;
  background: transparent;
